<template>
    <div>
        <!-- Initial dialog -->
        <b-modal
            no-stacking
            :id="id"
            ok-title="Withdraw"
            :ok-disabled="withdrawTimeLocked"
            @show="onModalShow"
            @ok="onModalOk"
            :title="'Withdraw from ' + poolTitle"
        >
            <div class="centered">
                <div v-if="loaded && !withdrawTimeLocked">
                    <p>Withdraw VITE from the prize pool.</p>
                    <b-input-group>
                        <b-input-group-prepend>
                            <b-button
                                variant="outline-success"
                                v-on:click="setInputMin()"
                                >Clear</b-button
                            >
                        </b-input-group-prepend>
                        <b-form-input
                            type="range"
                            placeholder="0"
                            v-model="inputamount"
                            min="0"
                            :max="userDepositBalance_basic"
                            step="0.1"
                        />
                        <b-input-group-append>
                            <b-button
                                variant="outline-success"
                                v-on:click="setInputMax()"
                                >Max ({{
                                    String(userDepositBalance_basic)
                                }})</b-button
                            >
                        </b-input-group-append>
                    </b-input-group>
                    <br />
                    <b-input-group append="VITE">
                        <b-form-input type="number" v-model="inputamount" />
                    </b-input-group>
                    <br />
                    <p>
                        <i
                            >After withdrawing, any remaining stake will still
                            be eligible for prizes, but withdrawn funds will
                            <strong>not</strong>.</i>
                    </p>
                    <p>
                        <i
                            >Also note that if your remaining balance is less than the pool's minimum deposit, <strong>all</strong> funds will be withdrawn.
                        </i>
                    </p>                    
                </div>

                <div v-if="withdrawTimeLocked">
                    <p>
                        You have deposited too recently to withdraw funds.<br />
                        Please try again in {{ timeToUnlock_string }}.
                    </p>
                </div>
            </div>
        </b-modal>

        <b-modal
            :id="waitingModalId"
            ok-variant="secondary"
            ok-title="Cancel"
            no-stacking
            no-close-on-esc
            no-close-on-backdrop
            title="Waiting for confirmations..."
        ><!-- hide-header-close -->
            <div class="centered">
                <b-spinner label="Spinning"></b-spinner>
                <br />
                <span v-if="waiting">
                    <i> Please accept the transaction on your Vite Wallet. </i>
                </span>
                <span v-if="!waiting">
                    Waiting for confirmations: {{ nconfirm }}/{{confirmationsNeeded}}
                </span>
                <br />
            </div>
            <template v-slot:modal-footer>
            &nbsp;
            </template>
        </b-modal>

        <!-- Success dialog -->   
        <b-modal :id="successModalId" ok-only no-stacking title="Succeeded">
            <div class="centered">Withdraw successful!</div>
        </b-modal>

        <!-- Fail dialog -->   
        <b-modal :id="failureModalId" ok-only no-stacking title="Failure">
            <div class='centered'>
                Withdraw failed.
            </div>
        </b-modal>
    </div>
</template>

<script>
import { callWithdraw } from "./PrizePoolAPI";
import {
    getAccountBlockByHash,
    getSnapshotChainHeight,
} from "../../services/viteprovider";
import BigNumber from "../../utils/bigNumber.js";
import { mapGetters } from 'vuex';
import {secondsToDhms} from '../../utils/misc.js';


function retry(fn, n) {
    let promise = Promise.reject();
    for (let i = 0; i < n; i++) {
        promise = promise.catch(() => fn());
    }
    promise.catch(() => {
        throw new Error(`Failed retrying ${n} times`);
    });
    return promise;
}

export default {
    name: "WithdrawModal",
    data() {
        return {
            loaded: false,
            inputamount: 0,
            waiting: false,
            nconfirm: 0,
            withdrawTimeLocked: true,
            timetounlock: 0,
            confirmationsNeeded: 5,
        };
    },
    props: {
        id: String,
    },
    computed: {
        ...mapGetters(['getActivePool']),
        pool(){ return this.getActivePool },        
        contractAddress() { return this.pool.contractAddress || ''},
        poolTitle() { return this.pool.title || ''},
        poolTokenId() { return this.pool.tokenId || ''},
        userLastDepositTime() { return this.pool.userLastDepositTime || ''},
        userDepositBalance(){ return this.pool.userDepositBalance || '' },
        userDepositBalance_basic(){ return BigNumber.toBasic(this.userDepositBalance, 18) }, /// FIXME: NEEDS DECIMALS!
        userAddress(){ return this.$store.state.vcstore.userAddress || ''},
        lockTime(){ return this.pool.lockTime || '' },
        //
        timeToUnlock_string(){ return secondsToDhms(this.timetounlock) },
        //        
        waitingModalId() {return this.id + "_waiting";},
        successModalId() {return this.id + "_succcess";},
        failureModalId() {return this.id + "_failure";},
    },
    methods: {

        onModalShow() {
            this.$store.dispatch("updatePrizePool", this.contractAddress);  
            this.$store.dispatch("updatePrizePoolUserInfo", this.contractAddress);  

            this.withdrawTimeLocked = true;

            getSnapshotChainHeight().then((now) => {
                        console.log("chainheight", now);
                        console.log("locktime", Number(this.lockTime));
                        console.log("usertime", Number(this.userLastDepositTime));
                        this.timetounlock =
                            Number(this.userLastDepositTime) +
                            Number(this.lockTime) -
                            Number(now);
                        console.log("timetounlcok", this.timetounlock);
                        if (this.timetounlock < 0) {
                            this.withdrawTimeLocked = false;
                        }
                        console.log(this.withdrawTimeLocked);
                    });
                    this.loaded = true;
                
        },
        onModalOk() {
            
            // waiting for user to sign transaction
            this.waiting = true;
            this.nconfirm = 0;
            this.$bvModal.show(this.waitingModalId);
            

            callWithdraw(
                this.contractAddress,
                BigNumber.toMin(this.inputamount, 18)
            )
                .then((res) => {
                    console.log("res1:", res);
                    this.waiting = false;
                    const fn = () =>
                        new Promise((resolve, reject) => {
                            getAccountBlockByHash(res.hash).then((blk) => {
                                console.log("promise:", blk);
                                this.nconfirm = blk.confirmations
                                    ? blk.confirmations
                                    : 0;
                                if (Number(blk.confirmations) > this.confirmationsNeeded) {
                                    resolve(true);
                                } else {
                                    setTimeout(reject, 2000);
                                }
                            });
                        });
                    return retry(fn, 10);
                })
                .then(() => {
                    this.$bvModal.show(this.successModalId);
                })
                .catch(() => {
                    this.$bvModal.show(this.failureModalId);
                })                
                .finally(()=>{
                    // update balances
                    this.$store.dispatch('updatePrizePool', this.contractAddress)
                    this.$store.dispatch('updatePrizePoolUserInfo', this.contractAddress)
                    // FIXME: unlock any global transaciton lock.
                });
        },
        setInputMax() {
            this.inputamount = this.userDepositBalance_basic;
        },
        setInputMin() {
            this.inputamount = 0;
        },
    },
};
</script>

<style scoped>
.centered {
    align-items: center;
    justify-content: center;
    text-align: center;
}
</style>