<template>
    <div>
        <!-- Initial dialog -->
        <b-modal
            no-stacking
            :id="id"
            :title="'Donate to ' + poolTitle"
            ok-title="Donate"
            @show="onModalShow"
            @ok="onModalOk"
        >
            <div class="centered">
                <div :v-if="loaded">
                    <p>Donate VITE into the prize pool.</p>
                    <b-input-group>
                        <b-input-group-prepend>
                            <b-button   
                                variant="outline-success"
                                v-on:click="setInputMin()"
                            >
                                Clear
                            </b-button>
                        </b-input-group-prepend>
                        <b-form-input
                            type="range"
                            placeholder="0"
                            v-model="inputamount"
                            min="0"
                            :max="userBalance_basic"
                            step="0.1"
                        />
                        <b-input-group-append>
                            <b-button
                                variant="outline-success"
                                v-on:click="setInputMax()"
                                >Max ({{ String(userBalance_basic) }})</b-button
                            >
                        </b-input-group-append>
                    </b-input-group>
                    <br />
                    <b-input-group append="VITE">
                        <b-form-input type="number" v-model="inputamount" />
                    </b-input-group>
                    <br />
                    <p>
                        <i>
                            <strong>WARNING:</strong> Donated funds can not be retreived! Donations will be awarded to the winner of the drawing.
                        </i>
                    </p>
                </div>
            </div>
        </b-modal>

        <!-- Waiting dialog -->   
        <b-modal
            :id="waitingModalId"
            no-stacking
            title="Waiting for confirmations..."
            no-close-on-esc
            no-close-on-backdrop
            
        ><!-- hide-header-close -->
            <div class="centered">
                <b-spinner label="Spinning" />
                <br />
                <span v-if="waiting">
                    <i> Please accept the transaction on your Vite Wallet. </i>
                </span>
                <span v-if="!waiting">
                    Waiting for confirmations: {{ nconfirm }}/{{ confirmationsNeeded}}
                </span>
                <br />
            </div>
            <template v-slot:modal-footer>
            &nbsp;
            </template>
        </b-modal>

        <!-- Success dialog -->   
        <b-modal :id="successModalId" ok-only no-stacking title="Succeeded">
            <div class="centered">Donation successful!</div>
        </b-modal>

        <!-- Fail dialog -->   
        <b-modal :id="failureModalId" ok-only no-stacking title="Failed">
            <div class="centered">Dontation failed.</div>
        </b-modal>
    </div>
</template>

<script>
// so i do want single modal for all
// and pass props to the modal as needed
// https://laracasts.com/discuss/channels/vue/vuejs-multiple-modals-nested-in-component-need-help
// a way of setting up logic for modals
// https://stackoverflow.com/questions/52005443/how-to-programmatically-inject-content-in-bootstrap-vue-modal-body-and-footer
// though same answer suggests multiple modals...
//
//import { mapState } from "vuex";
//import { mapGetters } from "vuex";
import { donateToContract } from "./PrizePoolAPI";
import { getAccountBlockByHash } from "../../services/viteprovider";
import BigNumber from "../../utils/bigNumber.js";
import { mapGetters } from 'vuex';

function retry(fn, n) {
    let promise = Promise.reject();
    for (let i = 0; i < n; i++) {
        promise = promise.catch(() => fn());
    }
    promise.catch((err) => {
        throw new Error(`Failed retrying ${n} times` + String(err));
    });
    return promise;
}

export default {
    name: "DonateModal",
    data() {
        return {
            loaded: false,
            inputamount: 0,
            waiting: false,
            nconfirm: 0,
            confirmationsNeeded: 5
        };
    },
    props: {
        id: String,
    },
    computed: {
        ...mapGetters(['getActivePool']),
        pool(){ return this.getActivePool },        
        contractAddress() { return this.pool.contractAddress || ''},
        poolTitle() { return this.pool.title || ''},
        poolTokenId() { return this.pool.tokenId || ''},
        userBalance(){ return this.$store.state.vcstore.balanceMap[this.poolTokenId] },
        userBalance_basic(){ return BigNumber.toBasic(this.userBalance, 18) }, /// FIXME: NEEDS DECIMALS!        
        //connected() { return this.$store.getters.connected },
        waitingModalId() {return this.id + '_waiting'},
        successModalId() {return this.id + '_succcess'},
        failureModalId() {return this.id + '_failure'},
    },
    methods: {
        onModalShow() {
            // update user balances            
            this.$store.dispatch("updateBalanceMap");            

            this.inputamount = 0;
        },
        onModalOk() {
            // FIXME: either initiate global tx lock here, or in thing that opens modal?

            // waiting for user to sign transaction
            this.waiting = true;
            this.nconfirm = 0;
            this.$bvModal.show(this.waitingModalId);

            donateToContract(
                this.contractAddress,
                BigNumber.toMin(this.inputamount, 18)
            )
                .then((res) => {
                    // waiting for enough confirmations
                    this.waiting = false;
                    
                    // retry with delay until enough confirmations.
                    const fn = () =>
                        new Promise((resolve, reject) => {
                            getAccountBlockByHash(res.hash).then((blk) => {
                                this.nconfirm = blk.confirmations
                                    ? blk.confirmations
                                    : 0;
                                if (Number(blk.confirmations) >= this.confirmationsNeeded) {
                                    resolve(true);
                                } else {
                                    setTimeout(reject, 2000);
                                }
                            });
                        });
                    
                    // retry(fn, 10) will resolve true if enough confirmations reached.
                    // If rejected, will take total time of 2*10 = 20 seconds
                    return retry(fn, 10);

                })
                .then(() => {
                    // request transaction resolved with enough confirmations
                    this.$bvModal.show(this.successModalId);                    
                })
                .catch(() => {
                    // request transaction failed.
                    this.$bvModal.show(this.failureModalId);
                })
                .finally(()=>{
                    // update balances
                    this.$store.dispatch('updatePrizePoolUserInfo', this.contractAddress)
                    // FIXME: unlock any global transaciton lock.
                });
        },
        setInputMax() {
            this.inputamount = this.userBalance_basic;
        },
        setInputMin() {
            this.inputamount = 0;
        },
    },
}

</script>

<style scoped>
.centered {
    align-items: center;
    justify-content: center;
    text-align: center;
}
</style>