
<template>

<div>
  
  <b-card no-body class='vtbg' style='border: none; display: flex; align-items: center; justify-content: center;'>
    <div class='blurbox py-3'>
    <h2 class='shadowtext'>{{name}}</h2>
    </div>
  </b-card>
<b-card no-body>    
    <b-tabs card>
        <b-tab title="Overview" active>

            <b-button :id="'pooltitle-target1' + _uid" variant="primary" class="mb-3 mt-1 mr-1" size="lg">
              Jackpot: <strong class='h3'>${{estimatedPrizeUSD}}</strong>&nbsp;&nbsp;({{estimatedPrizeToken}} {{prizeToken}})
            </b-button>
            <b-tooltip :target="'pooltitle-target1' + _uid" triggers="hover" placement="bottom">
              This is the <strong>expected</strong> Jackpot size at the time of drawing. The Jackpot increases daily due to voting rewards. Current size is <strong>{{currentPrizeAmount}} {{prizeToken}}</strong>.
            </b-tooltip>

            <b-button :id="'pooltitle-target2' + _uid" variant="primary" class="mb-3 mt-1 mx-2" size="lg">
              Staked: <strong class='h3'>{{totalStakeAmount}} {{prizeToken}}</strong>
            </b-button>
            <b-tooltip :target="'pooltitle-target2' + _uid" triggers="hover" placement="bottom">
              This is the total amount of {{prizeToken}} earning voting rewards for the Jackpot. 
            </b-tooltip>            

            
                        <b-card-text>
                            <b-progress height="2.5rem">      
                                <b-progress-bar :value="timeIndicatorValue" variant="success" striped :animated="true" >
                                    <span class="h5 m-0 shadowtext2">Time Until Drawing: <strong>{{timeUntilDrawingString}}</strong></span>
                                </b-progress-bar>
                            </b-progress>
                            <br>
                            <b-card>
                                <b-card-text>
                                  <slot></slot>                                
                                </b-card-text>
                            </b-card>
                        </b-card-text>



        </b-tab>

        <!--
        <b-tab title="Pool Details">          
                        <h3><b-badge class="mr-1" variant="primary">Current Pool Prize: {{currentPrizeAmount}} {{prizeToken}}</b-badge>    </h3>
                        <h3><b-badge class="mr-1" variant="primary">Effective APR: {{effectiveAPR}}% (estimated)</b-badge>    </h3>
                        <h3><b-badge class="mr-1" variant="info">Prize Eligible Deposits: {{eligibleStakeAmount}} Vite</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="dark">Number of Users: {{userCount}}</b-badge></h3>
                        <h3><b-badge class="mr-1" variant="danger">Total Deposits: {{totalStakeAmount}} {{stakeToken}}</b-badge></h3>                            
                        <h3><b-badge class="mr-1" variant="warning">DAO Reserve: {{daoStakeAmount}} {{prizeToken}}</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="warning">DAO Reserve Rate: {{daoReserveRate}}%</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="warning">Sponsor Deposits: {{sponsorStakeAmount}} {{prizeToken}}</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="secondary">Pool SBP: {{votingFor}}</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="dark">Time Between Drawings: {{timeBetweenDrawings}} seconds</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="dark">Time Funds Locked: {{timeFundsLocked}} seconds</b-badge></h3>    
                        <h3><b-badge class="mr-1" variant="dark">Your current odds: 1:{{ticketOdds}}</b-badge></h3>    
        </b-tab>
        -->

        <b-tab title="Pool Details">

          <div class="accordion" role="tablist">
            <AccordionItem accordionName="fjdalkdsafjkl">
              <template #titlebar>
                Current Jackpot: {{currentPrizeAmount}} {{prizeToken}}
              </template>
              This is the "jackpot" that will be sent entirely to the winner of the drawing. The prize pool value starts empty, and increases any time funds are directly sent to the prize pool contract. This includes voting rewards provided by SBPs in exchange for votes, as well as any donations sent to the contract.
            </AccordionItem>
            <AccordionItem variant="primary">
              <template #titlebar>
                Your current odds: {{ticketOdds}}
              </template>
              This shows <strong>your</strong> odds of winning at the next drawing. The odds are calculated based on your current deposit <span v-if="connected">({{userStakeAmount}} {{stakeToken}})</span> and the current prize eligible deposits ({{eligibleStakeAmount}} {{stakeToken}}).
            </AccordionItem>
            <AccordionItem variant="primary">
              <template #titlebar>
                Your current deposit: {{depositInfoString}}
              </template>
              This shows your current deposit into the pool. Each token deposited has a chance to win every drawing.
            </AccordionItem>


            


            <AccordionItem variant="info">
              <template #titlebar>
                Number of Users: {{userCount}}
              </template>
              This shows the number of unique user accounts that have active deposits in the pool.
            </AccordionItem>
            <AccordionItem variant="info">
              <template #titlebar>
                Prize Eligible Deposits: {{eligibleStakeAmount}} {{stakeToken}}
              </template>
              This shows the total deposit amount from all users. This does not include the DAO reserve, or deposits from sponsors.
            </AccordionItem>
            <AccordionItem variant="info">
              <template #titlebar>
                Total Staked: {{totalStakeAmount}} {{stakeToken}}
              </template>
              This shows the total amount in the pool, including deposits from users, sponsor deposits, the current prize, as well as the DAO reserve. This is the effective voting weight of the pool which determines the pool's gross income.
            </AccordionItem>

            <AccordionItem variant="warning">
              <template #titlebar>
                DAO Reserve: {{daoStakeAmount}} {{prizeToken}}
              </template>
              The DAO reserve are funds contributed by the VT DAO to increase the voting weight of the pool and thus the reward size. These funds are <i>not</i> eligible for earning prizes, but they increase the potential size of prizes. As the DAO reserve grows, prizes will increase in size. 
            </AccordionItem>

            <AccordionItem variant="warning">
              <template #titlebar>
                DAO Reserve Rate: {{daoReserveRate}}%
              </template>
              The DAO reserve rate is the percent of SBP income that is added to the VT DAO reserve. As the DAO reserve grows, prizes will increase in size.
            </AccordionItem>

            <AccordionItem variant="warning">
              <template #titlebar>
                Sponsor Deposits: {{sponsorStakeAmount}} {{prizeToken}}
              </template>
              These are funds contributed directly by sponsors to help increase the pool earnings and prize sizes. These funds are <i>not</i> eligible for earning prizes, but they increase the potential size of prizes.
            </AccordionItem>

            <AccordionItem variant="secondary">
              <template #titlebar>
                Pool SBP: {{votingFor}}
              </template>
                Snapshot Block Producers (SBPs) are an important component of the distributed proof-of-stake (DPoS) consensus mechanism used in the Vite network.
                SBPs earn rewards directly from the network for their contribution, but some SBPs also share these rewards with users that vote for the SBPs.

                <strong>{{votingFor}}</strong> is the SBP voted for by this ViteTogether pool, which in turn provides rewards that fund the pool's prizes.
            </AccordionItem>

            <AccordionItem variant="secondary">
              <template #titlebar>
                SBP Effective APR: {{effectiveAPR}}% (estimated)
              </template>
              This shows the estimated APR of the pool based on historical rate of rewards earned from voting for the current Snapshot Block Producer.
            </AccordionItem>

            <AccordionItem variant="secondary">
              <template #titlebar>
                Time Between Drawings: {{secondsToDhms(timeBetweenDrawings)}}
              </template>
              This is how frequently a winner is chosen from the pool. Note that the prize pool drawing can be initiated by <i>anyone</i> if needed, but the ViteTogether team initates a drawing when it is available.
            </AccordionItem>

            <AccordionItem variant="secondary">
              <template #titlebar>
                Time Funds Locked: {{secondsToDhms(timeFundsLocked)}}
              </template>
              After a user deposits funds into the pool, they are prevented from immediately withdrawing. This is done to ensure that all users benefitting from the pool also contribute to the pool's voting rewards, with the lock duration is usually chosen to be similar to the time between drawings.
              <br><br>
              This approach allows users the opportunity to join the pool instantly to participate in a large drawing, while also preventing gaming the system by withdrawing immediately afterwards.
            </AccordionItem>


              <AccordionItem variant="dark">
              <template #titlebar>
                Contract Information
              </template>
<!--This pool is powered by open source contract--> This pool's contract address is: <strong>{{contractAddress}}</strong>
            </AccordionItem>


          </div>

        </b-tab>
 

        <!--<b-tab title="Pool History / Stats" disabled>-->
        <b-tab disabled title="Pool Stats (coming soon!)">
        <b-card-text>Tab contents 3</b-card-text>
        </b-tab>

        <b-tab v-if="$store.getters.isAdmin" title="Admin">
        <b-button-toolbar>
                <b-button :disabled="!connected" class="ml-2" variant="warning" v-on:click="doCallDraw()">Do Drawing</b-button>
                <b-button :disabled="!connected" class="ml-2" variant="warning" v-on:click="sponsorClick()">Sponsor Pool</b-button>        
                <b-button :disabled="!connected" class="ml-2" variant="danger" v-on:click="donateClick()">Donate to Pool</b-button>        
                <b-button :disabled="!connected" class="ml-2" variant="danger" v-on:click="doSmallDeposit()">Donate 10 Vite as Award</b-button>        
        </b-button-toolbar>
        </b-tab>

    </b-tabs>

    <template #footer>
        <b-button-toolbar>
        <b-button :disabled="!connected" class="ml-2" variant="primary" @click="depositClick()">Deposit</b-button>
        <b-button :disabled="!connected" class="ml-2" variant="primary" @click="withdrawClick()">Withdraw</b-button>
        <b-button v-if="connected" disabled class="ml-2" variant="success">Current Stake: {{userStakeAmount}} {{stakeToken}}</b-button>
        <b-button v-if="!connected" class="ml-2" variant="secondary" v-on:click="connectClick">Connect Wallet</b-button>      
        </b-button-toolbar>
    </template>

</b-card>
</div>

</template>


<script>
 
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'

// primarily (all?) data loaded from contract.


import { mapGetters, mapActions } from 'vuex';
import { callDraw, testingSmallDeposit } from './PrizePoolAPI'
import { getSnapshotChainHeight } from '../../services/viteprovider';
import BigNumber from '../../utils/bigNumber.js';
import {secondsToDhms} from '../../utils/misc.js';
import AccordionItem from '../../components/AccordionItem.vue'
import { getTokenPriceByViteTokenId } from '../../services/coingecko'
//getUserInfo, getSponserInfo
// may want for animated numbers: https://github.com/nkoik/vue-animated-number



export default {
  name: 'PrizePoolItem',
  components: {
    AccordionItem
  },
  props:{
    name: String,
    contractAddress: String    
  },
  data() {
    return {
      tokenPrice: 0,
      f: null,
      updateTimeout: null,
      updateInterval: null,
      timeLeft: 0,
      timeLeftUpdateCount: 0
    }
},
  async created() {
    // here is where I ask for data from the API, and initiate a polling interval
    //console.log(this.contractAddress);    
    //const newData = await getPoolInfo(this.contractAddress);
    //console.log(newData);

    await this.$store.dispatch('addPrizePool', {contractAddress: this.contractAddress, title: this.name}) // TEST
    //console.log("created", this.$store.state.PrizePoolStore.pools[this.contractAddress].title)
    //console.log(this.$store.state.pools[this.contractAddress])

    this.tokenPrice = await getTokenPriceByViteTokenId('tti_5649544520544f4b454e6e40'); //FIXME: no hardcoding tti

    //this.$store.dispatch('updatePrizePool',this.contractAddress) // every 10 seconds lawl.

    // possibly should set up as setTimeout to avoid "catchup" issues.
    // also should 
    this.updateData()
    //this.updateInterval = setInterval(()=>{this.updateData()},10000);
    this.updateInterval = setInterval(this.updateData,10000);

    //this.updateTimeout = setInterval(this.updateTimeLeft, 1000)
    // here's example of the latter approach
    this.f = ()=>{this.updateTimeLeft(); this.updateTimeout = setTimeout(this.f,1000)};
    this.f();

    


    

    
  },
  beforeDestroy(){
    clearInterval( this.updateInterval );
    //clearTimeout( this.updateTimeout );
    clearInterval( this.updateTimeout );
  },
  //mounted(){
//    this.$store.addPrizePool(this.contractAddress) // TEST
//  },
  
  computed: {
        ...mapGetters(['connected']),
        pool(){ return this.$store.state.PrizePoolStore.pools[this.contractAddress] },
        userDepositBalance(){ return this.pool.userDepositBalance },
        userLastDepositTime(){ return this.pool.userLastDepositTime },
        depositBalance(){ return this.pool.depositBalance },        
        awardBalance(){ return this.pool.awardBalance },
        estimatedAwardBalance(){         
        let val1 = [this.depositBalance, this.sponsorBalance, this.daoDepositBalance, this.awardBalance].reduce(BigNumber.plus)
        let val2 = BigNumber.plus(BigNumber.multi(val1, BigNumber.BigNumber(0.15 * Math.floor(this.timeLeft / (24*60*60))/365 )) , this.awardBalance)// uses estimated APR 
        let val3 = BigNumber.toBasic(val2, 18, 2)
        return val3;
        },
        awardInterval(){ return this.pool.awardInterval },
        lockTime(){ return this.pool.lockTime },
        lastAwardTime(){ return this.pool.lastAwardTime },
        daoRate(){ return this.pool.daoRate },
        daoDepositBalance(){ return this.pool.ownerDepositBalance},
        userCount(){ return this.pool.userCount},
        sponsorBalance(){ return BigNumber.minus(this.pool.sponsorBalance, this.daoDepositBalance) },
        votingFor(){return this.pool.lastVote},
        ////////
        ////////
        ////////

        ticketOdds(){
          if( !this.connected ){
            return '(connect wallet to see odds)'
          }
          return this.depositBalance && this.userDepositBalance && "1:" + String(BigNumber.toBasic((this.depositBalance.minus(this.userDepositBalance).dividedBy(this.userDepositBalance)),0,1)) || ''
        },
        depositInfoString(){
          if( !this.connected ){
            return '(connect wallet to see deposit)'
          }
          return String(this.userStakeAmount) + " " + String(this.stakeToken)
        },      
        timeUntilDrawingString(){
          return secondsToDhms(this.timeLeft);
        },


        timeIndicatorValue(){
          return (1.0 - (this.timeLeft / this.pool.awardInterval))*100;
        },
        timeBetweenDrawings(){return this.pool.awardInterval},
        timeFundsLocked(){return String(this.pool.lockTime)},

        /// FIXME: not using these consistently, need to make sure which is which.
        /// also need to have this info pulled from store.
        prizeToken(){ return "Vite"},
        stakeToken(){ return "Vite"},
        
        currentPrizeAmount(){ return BigNumber.toBasic(this.awardBalance, 18, 2) },
        currentPrizeUSD(){
          return BigNumber.toBasic(this.awardBalance * this.tokenPrice ,18,2) // FIXME: change token id to match pool
        },  // need to update conversion rate
        estimatedPrizeUSD(){
          //return this.estimatedAwardBalance * this.tokenPrice
          return BigNumber.toBasic(this.estimatedAwardBalance * this.tokenPrice, 0, 4);
        },
        estimatedPrizeToken(){
          //return this.estimatedAwardBalance * this.tokenPrice
          return BigNumber.toBasic(this.estimatedAwardBalance, 0, 4);
        },
          // need to update conversion rate        
        // FIXME: stats / history? averagePrizeAmount(){ return "???"},
        //averagePrizeUSD(){ return "???"},
        userStakeAmount(){ return BigNumber.toBasic(this.userDepositBalance, 18, 2)}, 
        eligibleStakeAmount(){ return BigNumber.toBasic(this.depositBalance, 18, 2) },
        totalStakeAmount(){
          return BigNumber.toBasic([this.depositBalance, this.sponsorBalance, this.daoDepositBalance, this.awardBalance].reduce(BigNumber.plus),18,2)
        },
        daoStakeAmount(){return BigNumber.toBasic(this.daoDepositBalance,18)},
        sponsorStakeAmount(){return BigNumber.toBasic(this.sponsorBalance,18)},

        daoReserveRate(){ return BigNumber.toBasic(this.daoRate,2)},
        effectiveAPR(){ return "15"},
        
        
        //numberOfUsers add this for v2.0
  },
  methods:{
    ...mapActions(['requestViteConnectLogin']),
    secondsToDhms(arg){
          return secondsToDhms(arg)
    },
    depositClick(){      
      // FIXME: Could check global modal lock here?
      this.$store.dispatch('setActivePool', this.contractAddress);
      //this.$store.dispatch('updatea', this.contractAddress);
      this.$bvModal.show('depositModal')
    },
    withdrawClick(){
      this.$store.dispatch('setActivePool', this.contractAddress);
      this.$bvModal.show('withdrawModal')
    },
    connectClick(){
      if( !this.connected ){
        this.requestViteConnectLogin()
        return;
      }
      return;
    },
    donateClick(){
      this.$store.dispatch('setActivePool', this.contractAddress);
      this.$bvModal.show('donateModal')
    },
    sponsorClick(){
      this.$store.dispatch('setActivePool', this.contractAddress);
      this.$bvModal.show('sponsorModal')
    },
    updateData() {
      //console.log("data:",this)
      this.$store.dispatch('updatePrizePool',this.contractAddress)
      this.$store.dispatch('updatePrizePoolUserInfo', this.contractAddress)
      this.$store.dispatch('updatePrizePoolOwnerInfo', this.contractAddress)
      ////// FIXME: NEED TO GET DAO SPONSOR BALANCE  (i.e. owner!)

    },
    updateTimeLeft(){

      const mythis = this;
      

      if(this.timeLeftUpdateCount == 0){
        getSnapshotChainHeight().then((now) => {          
          mythis.timeLeftUpdateCount = 10;
          const newTimeLeft = Math.floor(Number(mythis.lastAwardTime)+Number(mythis.awardInterval)-Number(now));
          mythis.timeLeft = (newTimeLeft > 0) ? newTimeLeft : 0;
          //console.log(mythis, mythis.timeLeft)
        }).catch((err)=>{
          console.warn(err)
          mythis.timeLeft = mythis.awardInterval;
          //this.timeLeft = 0;
        });
      } else {
        mythis.timeLeftUpdateCount--;
        const nextTime = mythis.timeLeft - 1;
        mythis.timeLeft = (nextTime > 0) ? nextTime : 0;
      }
    },
    doCallDraw(){
      callDraw(this.contractAddress)
    },
    doSmallDeposit(){
      testingSmallDeposit(this.contractAddress);
    },
    /*
    secondsToDhms(seconds) {
      seconds = Number(seconds);
      const d = Math.floor(seconds / (3600*24));
      const h = Math.floor(seconds % (3600*24) / 3600);
      const m = Math.floor(seconds % 3600 / 60);
      const s = Math.floor(seconds % 60);

      const dDisplay = d > 0 ? d + (d == 1 ? " day, " : " days, ") : "";
      const hDisplay = h > 0 ? h + (h == 1 ? " hour, " : " hours, ") : "";
      const mDisplay = m > 0 ? m + (m == 1 ? " minute, " : " minutes, ") : "";
      const sDisplay = s > 0 ? s + (s == 1 ? " second" : " seconds") : "";
      const str = (dDisplay + hDisplay + mDisplay + sDisplay).replace(/,\s*$/, "")
      return str === "" ? "Now!" : str;
      //return dDisplay + hDisplay + mDisplay + sDisplay;
    }*/
  },
  watch: {
    connected(val){
      if(val){
        this.updateData()
      }
    }
  }
}

</script>

