    background: 
    linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.4),
      rgba(0, 127, 225, 0.7)
    ),
    url('~@/assets/cloudtile2.jpg');
    max-height: 20em;
    /*min-height: 5em;*/
    background-size: auto auto, auto 800%;
    background-position: bottom, bottom;
  }
  .shadowtext {
  text-align: center;
  color: white;
  text-shadow: 0 1px 0 black;
  }
