const CoinGecko = require('coingecko-api');
const CoinGeckoClient = new CoinGecko();

export const CoinGeckoTokenIdByViteTokenId = {
    'tti_5649544520544f4b454e6e40' : 'vite'
}

// get price data from coingecko
export async function getTokenPriceUSD(CG_CoinId){
    try{ 
        const params = {ids:CG_CoinId};
        const res = await CoinGeckoClient.simple.price(params)        

        console.log("pricelookup:", CG_CoinId, res.data[CG_CoinId].usd)

        return res.data[CG_CoinId].usd;
    } catch(err) {
        throw Error("Could not get price data:", err);
    }
}

const CGCache = {}
export async function getTokenPriceByViteTokenId(tokenId) {
    
    const CG_CoinId = CoinGeckoTokenIdByViteTokenId[tokenId];  

    if( Object.prototype.hasOwnProperty.call(CGCache, CG_CoinId) ) {
        return CGCache[CG_CoinId]
    } else {        
        const price = Number(await getTokenPriceUSD(CG_CoinId));
        CGCache[CG_CoinId] = price;
        return price
    }
}
